.student-form {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  gap: 10px;
}

.student-form input {
  flex: 1;
  padding: 8px 12px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.student-form button {
  padding: 8px 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.student-form button:hover {
  background-color: #0056b3;
}
